@use "../../../utilities/sizes";
@use "../../../utilities/colors";
.loginWrap {
  background-color: #fff;
  .login {
    height: 100vh;
    background-color: #0b601c80;
    display: flex;
    align-items: center;
    justify-content: center;

    .card {
      display: flex;
      margin-top: 8rem;
      width: 50%;
      background-color: #fff;
      border-radius: 10px;
      overflow: hidden;

      .left {
        flex: 1;
        background: linear-gradient(#043e0caa, #0b601cd2),
          url("https://plus.unsplash.com/premium_photo-1661730292317-eb780ab2c745?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80")
            center;
        background-size: cover;
        min-height: 600px;
        padding: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 30px;
        h1 {
          font-size: 3.5rem;
          line-height: 4rem;
          color: #fff;
        }
        p {
          font-size: 1.1em;
          color: #fff;
        }
        span {
          font-size: 1.1em;
          color: #fff;
        }
        button {
          width: 50%;
          padding: 10px;
          border: none;
          background-color: #fff;
          color: #05e105;
          font-size: 1.1em;
          font-weight: bold;
          cursor: pointer;
          border-radius: 3px;
          &:hover {
            color: #fff;
            background-color: #a8a6a6;
          }
        }
      }
      .right {
        flex: 1;
        padding: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 10px;
        h1 {
          padding-bottom: 2rem;
          color: #555;
        }
        form {
          display: flex;
          flex-direction: column;
          gap: 30px;
          .staffId {
            display: flex;
            align-items: center;
            position: relative;
            .loginInput {
              width: 100%;
            }
          }
          .loginInput {
            border: none;
            border-bottom: 1px solid lightgray;
            padding: 12px 10px;
            font-size: 1.1em;
            border-radius: 3px;
            &:focus {
              outline: 1px solid #0b601c80;
            }
            &::placeholder {
              color: #a8a6a6;
            }
          }
          button {
            width: 50%;
            padding: 10px;
            border: none;
            background-color: #05e105;
            color: white;
            font-weight: bold;
            cursor: pointer;
            transition: 0.4s;
            font-size: 1.1em;
            border-radius: 3px;
            &:hover {
              background-color: #04bb04;
            }
          }
        }
      }
    }
  }
}
